import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
		
		};
	},
	
	onLoad(options){



		this.cateId = options.tid;
		this.loadCateList(options.fid,options.sid);
		this.loadData(options.tid);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData(this.cateId,'refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid){
			let list = await this.$api.json('cateList');
			let cateList = list.filter(item=>item.pid == fid);
			
			cateList.forEach(item=>{
				let tempList = list.filter(val=>val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(tid,type='add', loading) {
			var that=this;
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			 uni.request({
			    header:{
			    'Content-Type':'application/json;charset=UTF-8',
			    'token':'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOiJoencifQ.YqWDSsPa_2FLTb9Zh2-UWmjq4tpus5Yiz_MHVe8etaI'
			    },
			    method:'POST',
			    url:"http://49.233.188.52:8090"+"/frontend/product/category/list",
			    //url:"http://localhost:8090"+"/frontend/product/category/list",
			    data: {
					"category":tid
				},
			    timeout:30000,
				dataType:"json",
			
			}).then(data => {//data为一个数组，数组第一项为错误信息，第二项为返回数据
						var [error, res]  = data;
						if(res.data.data==null){
							return
						}
						res.data.data.forEach(element => {
						    element.productInfo= JSON.parse(element.productInfo)
						});
						let goodsList=res.data.data
						if(type === 'refresh'){
							that.goodsList = [];
						}
						//筛选，测试数据直接前端筛选了
						if(that.filterIndex === 1){
							goodsList.sort((a,b)=>b.sales - a.sales)
						}
						if(that.filterIndex === 2){
							goodsList.sort((a,b)=>{
								if(that.priceOrder == 1){
									return a.price - b.price;
								}
								return b.price - a.price;
							})
						}
						
						that.goodsList = that.goodsList.concat(goodsList);
						
						//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
						that.loadingType  = that.goodsList.length > 20 ? 'nomore' : 'more';
						if(type === 'refresh'){
							if(loading == 1){
								uni.hideLoading()
							}else{
								uni.stopPullDownRefresh();
							}
						}
					})
		  
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = JSON.stringify(item);;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
